@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700&display=swap");

body {
  background: #ffffff;
  --header-backround-color: #ffffff;
  --mti-color: #0a1629;
  --separator: #000000;
  --color-buttons: #7d8592;
  --link-background: #f4f9fd;
  --tablitsa-background: #ffffff;
  --box-shadow: 0px 6px 30px 0px #0a16291a;
  --text-color: #000000;
  --add-buttons: #ecf2fe;

  --adUser-background: #ffffff68;

  --input-background: #ffffff82;

  --x-button: #f3f2f1;
  --lable-aduser-color: #8f8f8f;
  --modalka-backround: #ffffff;
  --modalka-backround-h2: #333333;
  --background-edit: #ffffff;
  --addUser-color: #000000;

  --background-edit: #ffffff;
  --addUser-color: #333333;
  --lable-aduser-color: #333333;
  --text-color: #000000;

  --select-backgroundColor: #ffffff;
  --select-color: #000000;

  --select-background: #ffffff;
  --select-paragraf: #333333;
  --select-hover: #deebfea0;

  --color-buttonss: #333;
  --sleketon-background: #e0e0e0;

  --input_masage_color: #000000;

  --userpopur: #ffffff;
  --userpopur1: rgb(0, 0, 0);
  --bor-cc: #ccc;

  --slider-container_background: #ffffff;
  --loading_color: #000000;

  --background-logoo: rgba(255, 255, 255, 0.63);

}
body.dark {
  background: #333333;
  --header-backround-color: #333333;
  --mti-color: #ffffff;
  --separator: #000000;
  --color-buttons: #a0aaba;
  --link-background: #0a1629;

  --input-background: #33333365;

  --tablitsa-background: #333333;
  --box-shadow: 0px 3px 15px 0px #4e4e4e;
  --text-color: #ffffff;
  --add-buttons: #3e8dfc67;

  --adUser-background: #3333336f;

  --x-button: #757575;
  --lable-aduser-color: #7d8592;
  --modalka-backround: #333333;
  --modalka-backround-h2: #ffffff;
  --background-edit: #333333;
  --addUser-color: #ffffff;

  --background-edit: #1e1e1e;
  --addUser-color: #f3f3f3;
  --lable-aduser-color: #f3f3f3;
  --text-color: #ffffff;

  --select-backgroundColor: #333;
  --select-color: #ffffff;

  --select-background: #333333;
  --select-paragraf: #ffffff;
  --select-hover: #b4b4b4;

  --color-buttonss: #ffffff;
  --sleketon-background: #e62121;
  --input_masage_color: #ffffff;
  --userpopur: #333333;
  --userpopur1: #ffffff;
  --bor-cc: #262626;

  --slider-container_background: #3333;
  --loading_color: #ffffff;

  --background-logoo: rgba(0, 0, 0, 0.61);
}
