.container {
  display: flex;
}

.userTable {
  width: 80%;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 50px auto;
  padding: 30px;
}
.noscroll {
  overflow: hidden;
}

.addButton {
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-weight: 500;
  border-radius: 5px;
  padding: 10px 20px;
  display: inline-block;
  transition: background-color 0.3s;
}

.userTable table {
  width: 100%;
  margin-top: 30px;
  border-collapse: collapse;
}

.SearchInput {
  height: 5vh;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding: 10px;
  margin: 0 10px;
  outline: none;
  transition: border-color 0.3s;
}

.SearchInput:focus {
  border-color: #6a0dad;
}

.block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.userTable table thead th {
  background-color: #333;
  color: white;
  padding: 10px;
}

.userTable table tbody td {
  padding: 10px;
  text-align: center;
  border-bottom: 1px solid #ddd;
}

.actionButtons button,
.actionButtons a {
  border: 0;
  outline: none;
  padding: 8px 12px;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
  transition: background-color 0.3s;
}

.actionButtons button {
  background: #ff4d4d;
}

.actionButtons button:hover {
  background: #e60000;
}

.actionButtons a {
  text-decoration: none;
}

.show {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.blur::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0); /* Цветной фон */
  backdrop-filter: blur(3px); /* Размытие */
  z-index: 1000;
  pointer-events: none;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1001;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  animation: modalFadeIn 0.3s forwards;
  box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.08),
    0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
  backdrop-filter: blur(3px); /* Размытие */
}

.modal.fadeOut {
  animation: modalFadeOut 0.3s forwards;
}
@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes modalFadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}

.close {
  color: #aaa;
  font-size: 28px;
  font-weight: bold;
  margin-left: 295px;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
