.container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
}
.addButton {
  color: #ffffff;
}
.addButtonLink {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  width: 100%;
  height: 100%;
}
.Links_Container {
  width: 900px;
  margin-right: 20px;
  overflow-x: auto;
}
.Links {
  display: flex;
  padding: 0px 0 10px 0;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 20px;
}

.Link {
  text-decoration: none;
  padding: 8px 25px;
  border-radius: 3px;
  font-family: "Nunito Sans";
  color: var(--mti-color);
  font-weight: 700;
  font-size: 16px;
  background-color: var(--link-background);
  border: none;
}
.Link:hover {
  background: #3f8cff;
  color: #ffffff;
}
.active {
  background: #3f8cff;
  color: #ffffff;
}

@media (min-width: 1250px) {
  .container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
  }
}

@media (max-width: 900px) {
  .Links {
    flex-wrap: nowrap !important;
    overflow-x: auto;
    overflow-y: auto;
    white-space: nowrap;
  }
  
}
@media (max-width: 480px) {
  .Links button {
    font-size: 13px;
  }
  .Links {
    margin-top: 10px;
  }
}
