.tableContainer {
  width: 100%;
  max-width: 1154px;
  margin: 0 auto;
  overflow-x: auto;
  height: 370px;
  background: transparent;
  scrollbar-width: none;
  scrollbar-color: #3f8cff transparent;
  padding: 10px;
}

.tableContainer::-webkit-scrollbar {
  height: 8px;
}

.tableContainer::-webkit-scrollbar-track {
  background: transparent; /* Прозрачный фон трека скроллбара */
  border-radius: 25px; /* Радиус границы трека скроллбара */
}

.tableContainer::-webkit-scrollbar-thumb {
  background: rgba(
    0,
    0,
    0,
    0.5
  ); /* Цвет ползунка скроллбара, можно изменить под свои нужды */
  border-radius: 25px; /* Радиус границы ползунка скроллбара */
}

.tableRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 30px;
  border-radius: 15px;
  box-shadow: 0px 6px 30px 0px #0a16291a;
  /* background-color: #fff; */
  box-shadow: var(--box-shadow);

  background-color: var(--tablitsa-background);
  margin-bottom: 10px;
}

.editButton,
.deleteButton {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  margin-left: 10px;
}

.editButton img,
.deleteButton img {
  width: 20px;
  height: 20px;
}
.div_divs_tablitsa {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  background: none;
}
.divs_tablitsa {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 70px;
  justify-content: space-evenly;
}
.divs_tablitsa_p {
  font-family: "Nunito Sans";
  font-weight: 400;
  size: 14px;
  line-height: 19.1px;
  color: #91929e;
}
.tableCell p {
  color: var(--text-color);
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Nunito Sans";
}
.container {
  display: flex;
  width: 100%;
  justify-content: center;
}
.searchContainer {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0px 30px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
  outline: none;
  background-color: var(--input-background);
  color: var(--input_masage_color);
}
input::placeholder {
  color: var(--input_masage_color);
}
.div1_input.focused {
  border: 1.5px solid #3f8cff; /* Цвет рамки при фокусе */
}
.button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.icon {
  width: 17px;
  height: 17px;
}
.div1_input {
  display: flex;
  width: 400px;
  justify-content: space-between;
  border: 1px solid #a9a9a9;
  border-radius: 4px;
  padding: 0 5px 0 0;
}
.div1_input input {
  border: none;
  outline: none;
  width: 100%;
}
.tableRow img {
  margin-right: 15px;
}
.mycomponent {
  width: 400px;
}

@media (max-width: 750px) {
  .tableContainer {
    overflow-x: auto;
    overflow-y: auto;
    max-height: 370px;
  }
  .mycomponent {
    width: 100%;
  }

  .tableRow {
    display: block;
  }

  .div_divs_tablitsa {
    display: block;
    width: 100%;
    gap: 10px;
  }

  .divs_tablitsa {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 0;
  }

  .divs_tablitsa_p {
    margin-right: 10px;
  }

  .editButton,
  .deleteButton {
    margin-left: 5px;
  }

  .div1_input {
    width: 100%;
  }

  .searchContainer {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .input {
    width: 100%;
  }
  .divs_tablitsa_amal {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .progress {
    display: none;
  }
}
.netdanix {
  color: var(--input_masage_color);
}
