.inputGroup input:-webkit-autofill,
.inputGroup input:-webkit-autofill:hover,
.inputGroup input:-webkit-autofill:focus,
.inputGroup input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0px 1000px var(--input-background) inset;
  -webkit-text-fill-color: var(--text-color);
}
.addUser {
  max-width: 80%;
  /* background: #ddd; */
  background-color: var(--background-edit);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 50px auto;
  padding: 40px 30px;
  position: relative; /* For positioning the close button */
}

.addUser h3 {
  font-family: "Nunito Sans";
  font-size: 20px;
  color: var(--addUser-color);
}

.addUserForm {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, auto);
  gap: 20px;
  margin-top: 20px;
}

.addUserForm > div {
  display: flex;
  flex-direction: column;
}

.inputGroup {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.inputGroup label {
  margin-bottom: 5px;
  font-weight: 500;
  color: var(--lable-aduser-color);
}

.inputGroup input,
.inputGroup .react-select__control {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.2s;
  background: var(--input-background);
  color: var(--color-buttonss); /* Задает цвет текста в инпуте */
}

.inputGroup input::placeholder {
  color: #807f7f;
}

.inputGroup input:focus,
.inputGroup .react-select__control:focus {
  border-color: #3f8cff;
  /* background-color: #3f8cff66; */
  /* color: #ffffff; */
}

.inputGroup button {
  border: 0;
  outline: none;
  background-color: #3f8cff;
  color: white;
  font-weight: 600;
  padding: 15px;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.inputGroup button:hover {
  background-color: #3f8cff;
  transform: translateY(-1px);
}

h3 {
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

.hh {
  font-size: 30px;
  cursor: pointer;
  position: absolute;
  top: 10px; /* Offset from the top edge */
  right: 10px; /* Offset from the right edge */
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  color: #aaa;
  transition: background 0.3s ease, color 0.3s ease;
}

.hh:hover {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media (max-width: 1123px) {
  .addUserForm {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(6, 1fr);
  }
}
@media (max-width: 768px) {
  .addUserForm {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(12, 1fr);
    gap: 5px;
  }
}
@media (min-width: 1250px) {
  .addUser {
    max-width: 1000px;
  }
}

.myComponentBackground {
  background-color: #f0f0f0; /* Здесь укажите нужный вам цвет */
  padding: 10px;
  border-radius: 5px;
  /* Добавьте любые другие стили, которые вам нужны */
}
