.tableContainer {
  width: 100%;
  max-width: 1154px;
  margin: 0 auto;
  overflow-x: auto;
  height: 370px;
  background: transparent;
  scrollbar-width: none;
  scrollbar-color: #3f8cff transparent;
  padding: 10px;
  border-radius: 20px;
}

.tableContainer::-webkit-scrollbar {
  height: 8px;
}

.tableContainer::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 25px;
}

.tableContainer::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 25px;
}

.tableRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 30px;
  border-radius: 15px;
  box-shadow: 0px 6px 30px 0px #0a16291a;
  box-shadow: var(--box-shadow);
  background-color: var(--tablitsa-background);
  margin-bottom: 10px;
}

.editButton,
.deleteButton {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  margin-left: 10px;
}

.editButton img,
.deleteButton img {
  width: 20px;
  height: 20px;
}

.div_divs_tablitsa {
  display: flex;
  width: 680px;
  background: none;
  justify-content: space-between;
}

.divs_tablitsa {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 70px;
  justify-content: space-evenly;
}

.divs_tablitsa_amal {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 70px;
  justify-content: space-evenly;
  width: 100px;
}

.divs_tablitsa_p {
  font-family: "Nunito Sans";
  font-weight: 400;
  size: 14px;
  line-height: 19.1px;
  color: #91929e;
}

.tableCell p {
  color: var(--text-color);
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Nunito Sans";
}

.tableCelll {
  display: flex;
  gap: 5px;
}

.container {
  display: flex;
  width: 100%;
  justify-content: center;
}

.searchContainer {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  padding: 0px 30px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.div1_input {
  display: flex;
  width: 400px;
  justify-content: space-between;
  border: 1px solid #a9a9a9;
  border-radius: 4px;
  padding: 0 5px 0 0;
}

.input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
  outline: none;
  background-color: var(--input-background);
  color: var(--input_masage_color);
  border: none;
  outline: none;
  width: 100%;
}
.div1_input.focused {
  border: 1.5px solid #3f8cff; /* Цвет рамки при фокусе */
}

input::placeholder {
  color: var(--input_masage_color);
}

