  color: #ffffff;
} */

/* input[type="date"] {
  color: var(--input_masage_color);
} */

/* input[type="date"]::placeholder {
  color: var(--input_masage_color);
} */

.button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.icon {
  width: 17px;
  height: 17px;
}
.progress {
  margin-right: 15px;
}

@media (max-width: 750px) {
  .tableContainer {
    overflow-x: auto;
    max-height: 370px;
  }

  .tableRow {
    display: block;
  }

  .div_divs_tablitsa {
    display: block;
    width: 100%;
    gap: 10px;
  }

  .divs_tablitsa {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 0;
  }

  .divs_tablitsa_p {
    margin-right: 10px;
  }

  .editButton,
  .deleteButton {
    margin-left: 5px;
  }

  .div1_input {
    width: 100%;
  }

  .searchContainer {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .input {
    width: 100%;
  }
  .divs_tablitsa_amal {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
  }
  .progress {
    display: none;
  }
}
.netdanix {
  color: var(--input_masage_color);
}
