.footer {
  background-color: rgba(63, 140, 255, 0.15);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-top: 10px; */
}

.footerDiv1 {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 1250px;
  gap: 30px;
  padding: 30px 200px 1px 30px;
}

.appLinks {
  display: flex;
  flex-direction: column;
  /* gap: 10px; */
}

.footerDiv1_1 {
  display: flex;
  justify-content: start;
  align-items: start;
  flex-direction: column;
  gap: 10px;
}
.footerDiv1_11 {
  display: flex;
  gap: 5px;
}

.appLinks img {
  width: 140px;
  padding: 5px;
}

.qrCode {
  width: 100px;
  padding: 3px 5px 5px 5px;
}

.footerDiv1_2 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 300px;
  gap: 10px;
}

.contact h3 {
  margin-bottom: 10px;
  margin-left: 0;
  font-family: "Montserrat";
  font-size: 14px;
  line-height: 21.94px;
  color: #7d8592;
  height: 20px;
}

.contactInfo {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  gap: 15px;
}

.contactInfo p {
  margin: 5px 0;
  color: #7d8592;
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 15px;
  line-height: 25px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.contactInfo p .phone {
  width: 25px;
  height: 25px;
}

.contactInfo p .email {
  width: 33px;
  height: 21px;
}

.company {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.company h3 {
  margin-bottom: 10px;
  margin-left: 0;
  font-family: "Montserrat";
  font-size: 14px;
  line-height: 21.94px;
  color: #7d8592;
  height: 25px;
}

.company ul {
  list-style: none;
  padding: 0;
}

.company ul li {
  margin: 2px 0;
  color: #7d8592;
  font-weight: 500;
  font-family: "Montserrat";
  font-size: 15px;
  line-height: 35px;
  cursor: pointer;
}

.khat {
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  border: 0.5px solid;
  margin: 7px 0;
}

.footerBottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1300px;
  padding: 7px 30px 12px;
}

.socialLinks {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.socialLinks a {
  margin: 0 10px;
  color: white;
  font-size: 24px;
}

.socialLinks .twitter {
  width: 26px;
  height: 18px;
}

.socialLinks .facebook {
  width: 25px;
  height: 26px;
}

.socialLinks .twitch {
  width: 30px;
  height: 25px;
}

.socialLinks .youtube {
  width: 28px;
  height: 28px;
}

.socialLinks .instagram {
  width: 25px;
  height: 26px;
}

.footerBottom {
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
}

.footerBottom p {
  color: #7d8592;
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 14px;
  line-height: 19.5px;
  align-items: center;
  display: flex;
  margin-right: 120px;
  word-spacing: 0.1px;
  letter-spacing: 0.001px;
}

/* Медиазапросы для адаптивности */
@media (max-width: 1250px) {
  .footerBottom {
    margin-right: 20px;
  }
}

@media (max-width: 1000px) {
  .footerBottom p {
    margin-right: 0;
  }

  .appLinks img {
    width: 140px;
  }

  .qrCode {
    width: 100px;
  }
  .contact h3 {
    font-size: 13px;
  }

  .contactInfo p {
    font-size: 16px;
  }
}

@media (max-width: 680px) {
  .footerDiv1 {
    display: flex;
    justify-content: space-between;
    align-items: start;
    padding: 30px 30px 0 30px;
  }

  .socialLinks {
    width: 100%;
  }
  .appLinks img {
    width: 120px;
  }

  .qrCode {
    width: 90px;
  }
  .socialLinks a {
    margin: 0;
    color: white;
    font-size: 2px;
  }
  .contactInfo {
    margin-left: 10px;
    gap: 5px;
  }
  .contactInfo p {
    font-size: 13px;
    display: flex;
    align-items: center;
  }
  .contactInfo p .phone {
    width: 22px;
    height: 22px;
  }

  .contactInfo p .email {
    width: 30px;
    height: 18px;
  }
  .socialLinks {
    width: 95%;
  }
}

@media (max-width: 550px) {
  .footerDiv1 {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 450px) {
  .footerDiv2 {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }

  .footerBottom p {
    width: 100%;
    justify-content: center;
  }
  .footerDiv1_2 {
    width: 100%;
  }
  .contact {
    width: 100%;
    gap: 0;
  }
}
