.inputField:-webkit-autofill:hover,
.inputField:-webkit-autofill:focus,
.inputField:-webkit-autofill,
.inputField:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0px 1000px var(--input-background) inset;
  -webkit-text-fill-color: var(--text-color);
}
.addData1Container {
  width: 100%;
  border-radius: 10px;
  font-family: Arial, sans-serif;
  position: relative;
  height: 270px;
}
.addData1Container:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.addData1Container h2 {
  text-align: start;
  color: var(--modalka-backround-h2);
  margin-bottom: 20px;
  font-size: 18px;
  font-family: "Rubik", sans-serif;
  margin-top: 5px;
}

.addData1Container form {
  display: flex;
  flex-direction: column;
  padding: 0 35px 20px 35px;
}

.addData1Container form .labelField {
  margin-bottom: 5px;
  color: var(--lable-aduser-color);
  font-family: "Rubik";
}

.addData1Container form .inputField {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 15px;
  outline: none;
  transition: border-color 0.2s;
  background: var(--input-background);
  color: var(--color-buttonss);
}

.addData1Container form .inputField:focus {
  border-color: #3f8cff;
  background-color: #3f8cff44;
}

.hh {
  text-decoration: none;
  background-color: #3f8cff;
  color: white;
  font-weight: 600;
  padding: 13px;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
  text-align: center;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  /* transform: translateY(-2px); */
}
.hh:hover {
  transform: translateY(-1px);
}

.closeButton {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 24px;
  color: #aaa;
  cursor: pointer;
  transition: color 0.3s;
}

.closeButton:hover {
  color: #000;
}
