/* Общие стили */
.container {
  width: 300px;
  background: var(--header-backround-color);
  height: 500px;
  margin-right: 30px;
}
.item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 10px;
  padding: 10px;
  gap: 10px;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
  width: 300px;
}

.item:hover {
  background: var(--add-buttons);
}

.item:hover .text {
  color: #3f8cff;
}

.item:hover .icon {
  filter: brightness(0) saturate(100%) invert(34%) sepia(83%) saturate(6200%)
    hue-rotate(189deg) brightness(98%) contrast(90%);
}

.separator {
  height: 2px;
  background-color: #002b5b;
  margin-top: -20px;
}

.icon {
  width: 40px;
  height: 35px;
  margin-right: 10px;
  background-size: cover;
  transition: filter 0.3s ease;
  color: #7d8592;
}
