.text {
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 500;
  line-height: 21.82px;
  letter-spacing: -1.5%;
  color: var(--color-buttons);
}

/* Стили для бургер-меню */
.burger_menu {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--header-backround-color);
  cursor: pointer;
  border-radius: 5px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.burger_menu div {
  width: 25px;
  height: 3px;
  background-color: var(--color-buttons);
  margin: 4px 0;
  transition: all 0.3s ease;
}

.burger_menu.open .line1 {
  transform: rotate(45deg) translate(5px, 5px);
}

.burger_menu.open .line2 {
  opacity: 0;
}

.burger_menu.open .line3 {
  transform: rotate(-45deg) translate(10px, -11px);
}
.noscroll {
  overflow: hidden;
}

/* Медиа-запрос для экранов с шириной 900 пикселей и меньше */
@media (max-width: 900px) {
  .container {
    display: none; /* Скрыть контейнер */
  }
  .burger_menu {
    display: flex; /* Показать бургер-меню */
    position: fixed; /* Закрепить бургер-меню в верхнем левом углу */
    top: 10px; /* Расположить вверху */
    left: 15px; /* Расположить слева */
    z-index: 1000; /* Убедиться, что контейнер будет на переднем плане */
  }

  .container.burger_menu_active {
    display: block; /* Показать контейнер при активном бургер-меню */
    position: fixed; /* Закрепить контейнер на экране */
    top: 56px; /* Расположить контейнер под бургер-меню */
    left: 0; /* Расположить слева */
    width: 100%; /* Занять всю ширину экрана */
    height: calc(100% - 57px);
    background: var(--header-backround-color); /* Установить фон */
    z-index: 1000; /* Убедиться, что контейнер будет на переднем плане */
    overflow-y: auto; /* Добавить вертикальную прокрутку */
  }
}
