:root {
  --select-color: #ffffff; /* Белый цвет для светлой темы */
  --select-background: #f0f0f0; /* Светлый фон */
  --select-paragraf: #000000; /* Черный цвет текста */
  --select-hover: #e0e0e0; /* Цвет при наведении */
}

/* Дополнительные стили, если требуется */
.customSelect .css-1wa3eu0-placeholder {
  color: red; /* Красный цвет для плейсхолдера */
  font-family: "Rubik", sans-serif; /* Шрифт Rubik */
}
.loading {
  color: var(--loading_color);
}
