.slider-container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 20px;
  /* background-color: var(--background-logoo); */
  margin-bottom: 10px;
  
}

.slick-next:before {
  content: none;
}
.slick-prev:before {
  content: none;
}
.slick-slide {
  display: flex;
  align-items: center;
  justify-content: center; /* Центрируем слайды */
}

.slide-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px; /* Пространство между слайдами */
}

.slide-item img {
  display: block; /* Убедитесь, что изображения не добавляют дополнительное пространство */
  width: 100%;
  height: auto; /* Автоматическая высота для сохранения пропорций */
}

/* Медиазапросы для мобильных устройств */
@media (max-width: 768px) {
  .slide-item {
    padding: 0 5px; /* Уменьшенное пространство между слайдами */
  }
  .slide-item img {
    max-width: 90%; /* Уменьшаем максимальную ширину изображений */
  }
}

@media (max-width: 480px) {
  .slide-item {
    padding: 0; /* Убираем пространство между слайдами для мобильных устройств */
  }
  .slide-item img {
    max-width: 90%; /* Уменьшаем ширину изображения */
  }
}
