.header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 10px 20px;
  background-color: var(--header-backround-color);
}

.logo {
  display: flex;
  align-items: center;
}

.mti {
  font-family: "Rubik", sans-serif;
  font-size: 25px;
  font-weight: 500;
  color: var(--mti-color);
  line-height: 24px;
  letter-spacing: -0.2%;
  align-items: center;
}

.logo img {
  height: 40px;
  margin-right: 10px;
}

.menu {
  display: flex;
  align-items: center;
}

.menu img {
  width: 35px;
  height: 35px;
  margin: 0 10px;
  cursor: pointer;
}
.separator {
  width: 100%;
  height: 2px;
  background-color: var(--separator);
  margin-top: 5px;
}

.header_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 30px;
}

.header_container h1 {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  line-height: 34px;
  font-weight: 500;
  font-size: 32px;
  letter-spacing: 1%;
  color: var(--mti-color);
}

.userContainer {
  position: relative;
}

.userPopup {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--userpopur);
  border: 1px solid var(--bor-cc);
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 2000;
  width: 150px;
}

.userPopup p {
  margin: 5px 0;
  font-family: "Courier New", Courier, monospace;
  color: var(--userpopur1);
}
.header_container .img11 {
  width: 100px;
  height: 110px;
}
.header_container .img22 {
  width: 110px;
  height: 103;
}

@media (min-width: 1250px) {
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1070px;
  }

  .header_container {
    display: flex;
    justify-content: center;
    gap: 200px;
  }
}

@media (max-width: 900px) {
  .header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background-color: var(--header-backround-color);
  }
  .separator {
    position: fixed;
    top: 49px; /* Высота заголовка */
    width: 100%;
    z-index: 999;
  }
  .header_container {
    margin-top: 50px;
  }

  .menu img {
    width: 30px;
    height: 30px;
  }

  .logo img {
    height: 35px;
  }
  .header_background {
    background-color: var(--header-backround-color);
  }
}

@media (max-width: 768px) {
  .header_container h1 {
    font-size: 24px;
    line-height: 28px;
  }

  .header_container .img11,
  .header_container .img22 {
    width: 90px;
    height: auto;
  }

  .menu img {
    width: 30px;
    height: 30px;
  }

  .logo img {
    height: 35px;
  }
  .header_background {
    background-color: var(--header-backround-color);
  }
}

@media (max-width: 480px) {
  .header_container {
    margin-top: 60px;
  }
  .header_container h1 {
    font-size: 18px;
    line-height: 22px;
  }

  .header_container .img11,
  .header_container .img22 {
    width: 60px;
    height: auto;
  }

  .menu img {
    width: 30px;
    height: 30px;
  }

  .logo img {
    height: 35px;
  }
  .header_background {
    background-color: var(--header-backround-color);
  }
  .header_container img {
    width: 50px;
  }
  .header_container h1 {
    font-size: 14px;
  }
}
